@import url('https://fonts.googleapis.com/css2?family=Kodchasan:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');

.pantalla-inicio {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	background-color: #00499d;
	font-family: Arial, Helvetica, sans-serif;
	color: rgb(233, 233, 233);
}

.pantalla-inicio div {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	gap: 2rem;

}

.content {
	position: absolute;
	height: 100vh;
	width: 50vh;
}

 h2 {
	font-family: 'Kodchasan', sans-serif;
}

.divTextSuperior {
	margin-bottom: 10%;
	font-size: 20px;
	width: 70%;
}

.textSuperior{
	color: rgba(255, 255, 255, 0.916);
	text-shadow: 1px 1px 2px rgba(0, 94, 255, 0.746), 0 0 1em rgba(1, 0, 70, 0.535), 0 0 0.2em rgb(0, 97, 252);
}

.divlogoFell {
	display: flex;
}

img {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 40%;
}

.divTextInferior {
	width: 70%;
	font-size: 20px;
	margin-top: 10%;
	word-wrap: break-word;
	text-align: justify;
}

.textInferior{
	color: rgb(255, 255, 255, 0.916);
	text-shadow: 1px 1px 2px rgba(0, 13, 255, 0.461), 0 0 1em rgba(13, 29, 62, 0.775), 0 0 0.2em rgba(0, 97, 252, 0.609);
}

.wave {
	position: absolute;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vh;
}

/* -------------ANIMACION--------------------------- */

.wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 43%;
	height: 20px;
	border-radius: 8px;
}

.border {
	/* border: 1px solid #272b33; */
	border-radius: 10px;
	height: 100%;
	width: 100%;
	padding: 1px;
}

.space {
	position: relative;
	overflow: hidden;
	height: 100%;
	width: 100%;
	margin: 0 auto;
	border-radius: 8px;
}

@keyframes loading {
	0% {
		left: -100%;
		background-color: #F3F8FC;
	}

	100% {
		left: 0%;
	}
}

.loading {
	position: absolute;
	height: 110%;
	width: 110%;
	background-color: rgb(233, 233, 233);
	animation: loading 3s steps(1000) infinite;
}