
.register {
	margin-top: 130px;
	width: 70%;
	flex-direction: column;
	align-items: center;
	color: rgb(81, 81, 81);
	font-family: 'Kodchasan', sans-serif;
}

.title {
	margin-bottom: 10px;
}

.inputGroup {
	display: flex;
	flex-direction: column;
	gap: 2em;
}

.input {
	border: 1px solid gray;
	border-radius: 5px;
	height: 2.8em;
	font-size: 1rem;
}

.btn-register {
	background-color:#00499de9;
	color: aliceblue;
	font-weight: bold;
	border: 1px solid gray;
	border-radius: 5px;
	height: 30px;
}

.linkALogin {
	margin-top: 10px;
}

.linkALogin a, a:visited {
	text-decoration:none ;
	color: rgb(81, 81, 81);;
}

/* laptop/desktop - de 1024px en adelante */
@media screen and (min-width: 1024px) {
	
	.register {
		width: 30%;
	}

}